<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <!-- 分页 -->
      <div class="demonstration-box">
        <el-pagination @size-change="handleSizeChange"
          :current-page.sync="currentPage"
          :total="pageParams.totalCount"
          :page-size="pageParams.size" 
          layout="total, prev, pager, next" 
        ></el-pagination>
      </div>
      <!-- 删除提示 -->
      <el-dialog title="编辑" :visible.sync="dialogVisible" width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 编辑提示 -->
      <el-dialog title="提示" :visible.sync="editDialog" width="30%">
        <div class="item edit-dialog">
          <span>订单状态：</span>
          <el-select v-model="editSelect" placeholder="全部">
              <el-option
                  v-for="item in orderList"
                  :key="item.id"
                  :label="item.dValue"
                  :value="item.dValue"
              ></el-option>
          </el-select>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialog = false">取 消</el-button>
          <el-button type="primary" @click="editSure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import { orderState } from 'data/listBody'
import queryString from "querystring"
import page from 'data/page'

const dataOption = {
  leftMenu: [
    { id:'1', name:'订单编号：', placeholder:'请输入订单编号', module:'name'},
    { id:'2', name:'商品名称：', placeholder:'请输入商品名称', module:'count'},
    { id:'3', name:'商品数量：', placeholder:'请输入商品数量', module:'price'},
    { id:'4', name:'校区名称：', placeholder:'请选择校区名称', module:'price'},
    { id:'5', name:'班级名称：', placeholder:'请选择班级名称', module:'price'},
    { id:'6', name:'带班老师：', placeholder:'请选择带班老师', module:'price'},
    { id:'7', name:'学员名称：', placeholder:'请输入学员名称', module:'price'},
    { id:'8', name:'订单时间：', placeholder:'请选择订单时间', module:'beginTime', type:'date'},
    { id:'9', name:'订单状态：', placeholder:'请选择订单状态', module:'price', type:'select', options: orderState},
  ],
  rightMenu: [
    { id:'r1', name:'查询'}
  ]
}

const dataTable = {
  table: [
    { label:'订单编号', prop:'name'},
    { label:'商品名称', prop:'count'},
    { label:'校区', prop:'price'},
    { label:'班级', prop:'time'},
    { label:'带班老师', prop:'time'},
    { label:'学员', prop:'time'},
    { label:'订单状态', prop:'time'},
    { label:'数量', prop:'time'},
    { label:'时间', prop:'time'},
  ],
  edit: [
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'goods',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      editDialog: false,
      tableList: [],
      orderList: orderState, //订单状态列表
      editSelect: '', //订单状态切换
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.search();
  },
  methods:{
    optchange(params){
      console.log(params.id + '--id被单击了')
      if(params.id === 'r1') this.search();
    },
    tabchange(params){
      console.log(params.id + '--id被单击了')
      this.id = params.data.id;

      // 编辑
      if(params.id === 't1') return this.editDialog = true;
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
    },
    sure(){
      this.search();
      this.dialogVisible = false;
    },
    editSure(){
      this.search();
      this.editDialog = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };

      const params = Object.assign({}, optParams, pageParams)
      this.tableList = [
        {name: '张三1', count: '100', price: '1200', time: '2018-01-10'},
        {name: '张三2', count: '100', price: '1200', time: '2018-01-10'},
        {name: '张三3', count: '100', price: '1200', time: '2018-01-10'},
      ]
      // this.loading = true;
      // const param = queryString.stringify(params || {});
      // let url = urls[code];
      // this.api.get(`/role`).then((res)=>{
        // this.loading = false;
        // this.data = res.data.list;
        // this.setPage(res.data)
        // this.dataInit();
      // }).catch((err) => {
      //   console.log(err)
      //   this.$message(err.message)
      // })
    },
    dataInit(){//数据初始化

    }
  }
}
</script>
 
