import _ from 'lodash';
import api from 'utils/api'
function sortBy(arr) { //排序初始化
    return _.sortBy(arr, (item) => {
        return item.sortId
    })
}

function getRoles(callback) { //得到角色列表
    let roles = [
        { id: -1, dKey: '', dValue: '全部' },
        { id: 0, dKey: 0, dValue: '超级管理员' },
        { id: 1, dKey: 1, dValue: '学员' },
        { id: 2, dKey: 2, dValue: '老师' },
        { id: 3, dKey: 3, dValue: '校长' },
        { id: 4, dKey: 4, dValue: '行政' },
    ]
    if (callback) callback(roles)
    return roles;
    // api.get(`/role`).then(res => {
    //   if (callback) callback(res.data)
    //   return res.data;
    // })
    // .catch(err => {
    //   console.log(err)
    // });
    // return [];
}

function getUsers(callback) { //得到用户列表
    api.get(`/user`).then(res => {
        let params = {
            id: '',
            name: '全部',
            roleId: ''
        }
        res.data.unshift(params);
        if (callback) callback(res.data)
        return res.data;
    })
    .catch(err => {
        console.log(err)
    });
    return [];
}

